import {
    Component,
    Input,
    AfterViewInit,
    OnInit,
    ElementRef,
    ContentChild,
    TemplateRef,
    Output,
    EventEmitter,
    Renderer2,
    OnDestroy
} from '@angular/core';
import { DropdownItem } from './dropdown.item.props';

@Component({
    selector: 'farris-dropdown',
    template: `
        <div
            class="farris-dropdown btn-group"
            [class.dropup]="position === 'top'"
            [class.dropdown]="position === 'bottom'"
            [class.dropleft]="position === 'left'"
            [class.dropright]="position === 'right'"
            [style.width]="width"
        >
            <span
                [class.btn]="!nest"
                [class.dropdown-item]="nest"
                [class.btn-lg]="size === 'large'"
                [class.btn-sm]="size === 'small'"
                [class.btn-primary]="type === 'primary'"
                [class.btn-success]="type === 'success'"
                [class.btn-warning]="type === 'warning'"
                [class.btn-danger]="type === 'danger'"
                [class.btn-info]="type === 'info'"
                (click)="leftButtonClick()"
                style="width:100%"
                *ngIf="splitButton"
                >{{ title }}</span
            >
            <span
                class="dropdown-toggle"
                [class.btn]="!nest"
                [class.dropdown-item]="nest"
                [class.dropdown-toggle-split]="splitButton"
                [class.btn-lg]="size === 'large'"
                [class.btn-sm]="size === 'small'"
                [class.btn-primary]="type === 'primary'"
                [class.btn-success]="type === 'success'"
                [class.btn-warning]="type === 'warning'"
                [class.btn-danger]="type === 'danger'"
                [class.btn-info]="type === 'info'"
                [class.btn-secondary]="type === 'secondary'"
                (click)="showDropMenu($event)"
                (mouseenter)="hoverDropdown()"
                (mouseleave)="hoverDropdown()"
                style="width:100%"
            >
                <span class="sr-only" *ngIf="splitButton"></span>
                {{ splitButton ? '' : title }}
            </span>
            <div class="dropdown-menu" [class.show]="show">
                <ng-template [ngTemplateOutlet]="dropdownTempl"></ng-template>
                <farris-dropdown-item
                    *ngFor="let item of model"
                    [model]="item"
                    (select)="selectItem($event)"
                >
                </farris-dropdown-item>
            </div>
        </div>
    `
})
export class FarrisDropdownComponent implements OnInit, AfterViewInit, OnDestroy {
    @Input() nest: boolean;
    // è®¾ç½®å®½ é«å±æ§
    _width: string | number;
    _height: string | number;
    @Input()
    get width() {
        // å¯¹åä¸ªåä½è¿è¡å¤ç
        return this._width;
    }
    set width(width: any) {
        this._width = this.resolveSize(width);
    }
    // ä¸ææ¡æ é¢
    @Input() title: string;
    // hoverå±ç¤ºä¸æåè¡¨
    @Input() hover: boolean;
    // ä¸ææ¡å°ºå¯¸
    @Input() size: string;
    // ä¸ææ¡ç±»å
    // tslint:disable-next-line:no-inferrable-types
    @Input() type: string = 'primary';
    // ç¹å»åè¡¨é¡¹ç®åæ¯å¦å³é­åè¡¨é¡¹
    // tslint:disable-next-line:no-input-rename
    // tslint:disable-next-line:no-inferrable-types
    @Input('hide-on-click') hideOnClick: boolean = true;
    // æé®ç»
    // tslint:disable-next-line:no-input-rename
    @Input('split-button') splitButton = false;
    // åè¡¨æ¾ç¤ºä½ç½®
    @Input() position = 'bottom';
    // åè¡¨é¡¹æ°æ®
    @Input() model: DropdownItem[] = [
        { label: 'é¡¹ç®ä¸', value: 'asdasd' },
        { label: 'é¡¹ç®äº', value: 'asdasd' },
        { label: 'é¡¹ç®ä¸', value: 'asdasd' }
    ];
    @ContentChild('dropdownTempl') dropdownTempl: TemplateRef<any>;
    // æé®åç»ä¹å  å·¦è¾¹æé®äºä»¶
    // tslint:disable-next-line:no-output-rename
    @Output('left-click') leftClick: EventEmitter<any> = new EventEmitter<
        any
    >();
    @Output() select: EventEmitter<any> = new EventEmitter<any>();
    // ä¸æåè¡¨æ¾ç¤ºæèéèè§¦åçäºä»¶
    // tslint:disable-next-line:no-output-rename
    @Output('visible-change') visibleChange: EventEmitter<
        any
    > = new EventEmitter<any>();
    dropdownButton: HTMLButtonElement;
    menu: HTMLDivElement;
    // åè¡¨é¡¹çæ¾ç¤ºä¸å¦
    show = false;
    globalListenFunc: Function;
    menuListenFunc: Function;
    constructor(private el: ElementRef, private renderer: Renderer2) {}
    ngOnInit(): void {
        this.menu = this.el.nativeElement.querySelector('.dropdown-menu');
    }
    ngAfterViewInit(): void {}
    ngOnDestroy(): void {
        if (this.globalListenFunc) {
            this.globalListenFunc();
        }
    }
    // ç¹å»åè¡¨é¡¹æ¾ç¤ºä¸å¦
    showDropMenu(event?: Event) {
        if (this.hover) {
            return;
        }
        if (event) {
            event.stopPropagation();
        }
        this.show = !this.show;
        this.visibleChange.emit();

        this.globalListenFunc = this.renderer.listen(
            'document',
            'click',
            () => {
                this.closeDropMenu();
            }
        );
        // å¦æä¸è®¾ç½®hide-on-click,åè¯´æç¹å»ä¸æä¹åä¸æä¸æ¶å¤±
        if (!this.hideOnClick) {
            this.menuListenFunc = this.renderer.listen(
                this.menu,
                'click',
                e => {
                    e.stopPropagation();
                }
            );
        }
    }
    // ç¹å»åç´ ä¹å¤  åè¡¨é¡¹æ¶å¤±
    closeDropMenu() {
        if (this.hover) {
            return;
        }
        this.show = false;
        this.visibleChange.emit();
        // è§£é¤äºä»¶ç»å®
        if (this.globalListenFunc) {
            this.globalListenFunc();
        }
        if (this.menuListenFunc) {
            this.menuListenFunc();
        }
    }
    // hover å®ç°
    hoverDropdown() {
        if (!this.hover) {
            return;
        }
        this.show = !this.show;
        this.visibleChange.emit();
    }
    /**
     * å¼¹åºäºä»¶
     */
    selectItem(model) {
        this.select.emit(model);
    }
    leftButtonClick() {
        this.leftClick.emit();
    }
    resolveSize(size) {
        const regex = /px|em|rem|pt|%/;
        // è¯´ææ¯å­ç¬¦ä¸²
        return regex.test(size)
            ? `${parseInt(size, 10)}${size.match(regex)[0]}`
            : `${size}px`;
    }
}
