import {
    Directive,
    Input,
    Output,
    HostBinding,
    EventEmitter,
    ElementRef,
    Renderer2,
    OnDestroy,
    OnInit
} from '@angular/core';
import { Observable, Subject } from 'rxjs';
@Directive({
    selector: '[fDropdown]',
    exportAs: 'fDropdown'
})
export class FDropdownDirective implements OnDestroy, OnInit {
    toggleElement: any;
    documentClickEventListener: any;
    selfDefineEventListener: any;
    private _seflEl: HTMLElement;
    // tslint:disable-next-line:no-input-rename
    private _internalOpen = false;
    // è®¡ç®å®½åº¦
    private _calculateMenu = false;
    // çº æ­£ä½ç½®çåç§ æwindowåparentä¸¤ä¸ªå¼
    @Input() rectifyReference;
    // çº æ­£çºµåä½ç½®çåç§ 
    @Input() rectifyReferenceV;
    // æ¯å¦èªå¨çº æ­£ä½ç½®
    @Input() autoRectify = false;
    // å¨å¤é¢å¼ºå¶æ§å¶å³é­ç¶æ
    @Input('forceState')
    set forceState(value: Array<boolean>) {

        if (value && value.length) {
            this.close();
        }
    }

    @Input('open')
    set openState(value: boolean) {
        this._internalOpen = value[0];
        this.isOpenState.next(value[0]);
    }
    @Output() dpChangeEvent = new EventEmitter<boolean>();
    private _placement = 'bottom'; // è®°å½ position

    @Input() isSubDP = false; // æ¯å¦æ¯å­ä¸æ

    @Input('placement')
    set placement(value) {
        // å¦æç¸ç­
        if (!value) {
            return;
        }
        //
        if (value !== this._placement) {
            const newClsName = this._getClsName(value);
            const oldClsName = this._getClsName(this._placement);
            this.render.removeClass(this._seflEl, oldClsName);
            this.render.addClass(this._seflEl, newClsName);
            this._placement = value;
        }
    }
    get placement(): string {
        return this._placement;
    }

    // @HostBinding('class.show')
    get isOpen(): boolean {
        return this._internalOpen;
    }

    private isOpenState = new Subject();


    @HostBinding('class.dropdown-submenu')
    get submenuCls(): boolean {
        return this.isSubDP;
    }
    @HostBinding('class.dropdown')
    get dropdownCls(): boolean {
        return !this.isSubDP;
    }
    private ngZone;

    constructor(
        elementRef: ElementRef,
        private render: Renderer2
    ) {
        this._seflEl = elementRef.nativeElement;
        this.isOpenState.subscribe((state: boolean) => {
            if (this._internalOpen !== state) {
                this._internalOpen = state;
                if (this._internalOpen) {
                    this.render.addClass(this._seflEl, 'show');
                } else {
                    this.render.removeClass(this._seflEl, 'show');
                }
            }
        });
    }
    ngOnInit() {
    }
    getNativeElement() {
        return this._seflEl;
    }
    private bindDocumentEvents() {
        //   this.ngzone.runOutsideAngular(() => {
        this.documentClickEventListener = this.onDocumentClick.bind(this);
        document.addEventListener('click', this.documentClickEventListener);
        // ç»å®è¢«æä½nativeæ¶è§¦å
        this.selfDefineEventListener = this.onSelfDefineHandler.bind(this);
        this._seflEl.addEventListener('selfClose', this.selfDefineEventListener);
        // });
    }

    private onSelfDefineHandler() {
        this.close();
    }
    private unbindDocumentEvents() {
        if (this.documentClickEventListener) {
            document.removeEventListener(
                'click',
                this.documentClickEventListener
            );
            this.documentClickEventListener = null;
        }
        if (this.selfDefineEventListener) {
            this._seflEl.removeEventListener(
                'selfClose',
                this.selfDefineEventListener
            );
            this.selfDefineEventListener = null;
        }
    }
    private onDocumentClick(event: MouseEvent) {
        // å¦æå·²ç»å³é­ï¼ä¸éè¦åååº
        if (!this._internalOpen) {
            return;
        }
        if (event.button !== 2 && !this.isEventFromToggle(event)) {
            this.close();
        }
    }

    // å¤æ­menuå±å¼æ¶æ¯å¦è¦è®¡ç®
    needToCalculate() {
        return this.autoRectify && this._calculateMenu;
    }
    resetCalculate(value) {
        this._calculateMenu = value;
    }
    open() {

        if (!this._internalOpen) {
            this.closeSiblingDropdowns();
            this._calculateMenu = true;
            this.render.addClass(this._seflEl, 'show');
            this.isOpenState.next(true);
            this.dpChangeEvent.emit(true);
            // æ§è¡ç»å®äºä»¶
            this.bindDocumentEvents();
        }
    }

    close() {
        if (this._internalOpen) {
            this._calculateMenu = false;
            this.render.removeClass(this._seflEl, 'show');
            this.isOpenState.next(false);
            this.dpChangeEvent.emit(false);
            this.unbindDocumentEvents();
        }
    }

    toggle() {
        if (this._internalOpen) {
            this.close();
        } else {
            this.open();
        }
    }
    getOpenState() {
        return this.isOpenState;
    }
    ngOnDestroy() {
        this.isOpenState.unsubscribe();
        this.unbindDocumentEvents();
    }
    private closeSiblingDropdowns() {
        let dpParentEl = this._seflEl.parentNode;
        if (dpParentEl && dpParentEl['className'].indexOf('dropdown-menu') > -1) {
            let dropdowns = dpParentEl.querySelectorAll('[fDropdown]');
            if (dropdowns && dropdowns.length > 1) {
                for (var k = 0; k < dropdowns.length; k++) {
                    if (dropdowns[k].className.indexOf('show')) {
                        // è§¦åäºä»¶è¯è¯
                        this.compatibleDispatchEvent(dropdowns[k], 'selfClose');
                        // dropdowns[k].dispatchEvent(new Event('selfClose'));
                    }
                }
            }
        }
    }
    private compatibleDispatchEvent(eventEl, eventName) {
        var event;
        if (typeof Event === "function") {
            event = new Event(eventName);
        } else {
            event = document.createEvent("Event");
            event.initEvent(eventName, false, false);
        }
        eventEl.dispatchEvent(event);
    }
    private _getClsName(position: string) {
        let className = '';
        switch (position) {
            case 'top-right':
            case 'top':
                // æä¸ï¼æä¸-æå³
                className = 'dropup';
                break;
            case 'top-left':
                // æä¸-æå·¦
                className = 'dropup-left';
                break;
            case 'left-bottom':
            case 'left':
                // æ¨ªåââæå·¦ââæä¸
                className = 'dropleft';
                break;
            case 'left-top':
                // æ¨ªåââæå·¦ââæä¸
                className = 'dropleft-up';
                break;
            case 'right-bottom':
            case 'right':
                // æ¨ªåââæå³ââæä¸
                className = 'dropright';
                break;
            case 'right-top':
                // æ¨ªåââæå³ââæä¸
                className = 'dropright-up';
                break;
            case 'bottom-left':
                // æä¸ââæå·¦
                className = 'dropdown-left';
                break;
            case 'bottom-right':
                className = 'dropdown';
                break;
            default:
                // æä¸ï¼æä¸ââæå³
                className = 'dropdown';
        }
        return className;
    }
    private isEventFromToggle(event: MouseEvent) {
        const result =
            this.toggleElement && this.toggleElement.contains(event.target);
        return result;
    }

    getRectifyReferenceEl() {
        
        var resultWidth = window.innerWidth, resultHeight = window.innerHeight;
        // æ¨ªåè®¡ç®
        if (this.rectifyReference) {
            resultWidth = this.rectifyReference.getBoundingClientRect().right;
        }
        // çºµåè®¡ç®
        if (this.rectifyReferenceV) {
            resultWidth = this.rectifyReference.getBoundingClientRect().bottom;
        }
        return { width: resultWidth, height: resultHeight };

    }
}

